<template>
  <v-container>
    <v-row class="text-center">
      <v-col cols="6">
        <v-autocomplete
          v-model="user"
          label="Encontre seu usu치rio"
          :items="userlist"
          :loading="userloading"
          :search-input.sync="usersearch"
          item-text="login"
        />
      </v-col>
      <v-col cols="6">
        <v-select
          v-model="repo"
          :items="repolist"
          :loading="repoloading"
          item-text="name"
          label="Selecione um reposit칩rio"
          return-object
          single-line
        />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import {debouncerDecorator} from '@/helpers/debouncer.js'
  import {api} from '@/api/api.js'

  export default {
    data: () => ({
      user: null,
      repo: null,
      usersearch: null,
      userlist: [],
      repolist: [],
      userloading: false,
      repoloading: false
    }),
    methods: {
      procuraUsuariosGithub: debouncerDecorator(async function () {
        this.userloading = true
        const data = await api.search_users(this.usersearch)
        this.userlist = data.items
        this.userloading = false
      }, 500),
      async listaRepositorios() {
        this.repoloading = true
        const data = await api.list_repos(this.user)
        this.repolist = data
        this.repoloading = false
      }
    },
    watch: {
      usersearch() {
        this.procuraUsuariosGithub()
      },
      user() {
        this.listaRepositorios()
      },
      repo() {
        console.log(this.repo)
      }
    },
  }
</script>
